import React from "react"
import PropTypes from "prop-types"
import { Helmet } from "react-helmet"
import { useStaticQuery, graphql } from "gatsby"

function SEO({ description, lang, meta, title, canonical, url, imageURL}) {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            description
            author
          }
        }
      }
    `
  )

  const metaDescription = description || site.siteMetadata.description

  return (
    <Helmet
      htmlAttributes={{
        lang,
      }}
      title={'Pratiiti Luminaries | '+title}
      titleTemplate={`%s`}
      meta={[
        {
          name: `description`,
          content: metaDescription,
        },
        {
          name: `og:site_name`,
          content: `Career Counsellor`
        },
        {
          property: `og:title`,
          content: title,
        },
        {
          property: `og:url`,
          content: url,
        },
        {
          property: `og:description`,
          content: metaDescription,
        },
        {
          property: `og:type`,
          content: `website`,
        },
        {
          name: `twitter:card`,
          content: metaDescription,
        },
        {
          name: `twitter:site`,
          content: '@pratiiti1',
        },
        {
          name: `twitter:creator`,
          content: '@pratiiti1',
        },
        {
          name: `og:image`,
          content: imageURL,
        },
        {
          name: `twitter:title`,
          content: title,
        },
        {
          name: `twitter:description`,
          content: metaDescription,
        },
        {
          name: `twitter:image:src`,
          content: imageURL,
        },
        {
          name: `twitter:image`,
          content: imageURL,
        },
        {
          name: `facebook:title`,
          content: title,
        },
        {
          name: `facebook:description`,
          content: metaDescription,
        },
        {
          name: `distribution`,
          content: 'global',
        },
        {
          name: `Content-Language`,
          content: 'EN',
        },
        {
          name: `geo.region`,
          content: 'IN',
        },
        {
          name: `RATING`,
          content: 'Safe for Kids',
        },
        {
          name: `robots`,
          content: 'index, follow',
        },
      ].concat(meta)}
      link={
         canonical
         ? [{ rel: 'canonical', key: canonical, href: canonical }]
         : []
       }
    />
  )
}

SEO.defaultProps = {
  lang: `en`,
  meta: [],
  description: ``,
  canonical: '',
  url: 'https://www.pratiiti.com',
  imageURL: 'https://storage.googleapis.com/production-asset-bucket/pratiiti-website-assets/images/logo.png',
}

SEO.propTypes = {
  canonical: PropTypes.string,
  description: PropTypes.string,
  lang: PropTypes.string,
  meta: PropTypes.arrayOf(PropTypes.object),
  title: PropTypes.string.isRequired,
  url: PropTypes.string,
  imageURL: PropTypes.string,
}

export default SEO
